import InputError from "components/InputError";
import Label from "components/Label";
import OTPInput, { ResendOTP } from "otp-input-react";
import { useState } from "react";
import React from "react";
import { ILabelProps, IErrorStyleProps } from "components/interface/Input";
import SimpleButton from "components/SimpleButton/SimpleButton";
import Text from "components/Text/Text";
import { BUTTON_STYLE_TYPE } from "utils/Constant";
export interface IBaseOTPCommonProps
  extends React.InputHTMLAttributes<HTMLInputElement>,
    ILabelProps,
    IErrorStyleProps {
  otpType?: string;
  resendOTPClassName?: string;
  autoFocus?: boolean;
  OTPLength: number;
  isTimerComplete?: boolean;
  onResendClick: (e?: any) => void;
  hideErrorRow?: boolean;
  validation?: object;
  errorClassNames?: string;
  styleResendObj?: object;
  hideResendOTP?: boolean;
  showTimer?: boolean;
}
const BaseOTPBox: React.FC<IBaseOTPCommonProps> = (props: IBaseOTPCommonProps) => {
  const {
    hideLabel,
    hideResendOTP,
    showTimer,
    label,
    labelClassNames,
    isTimerComplete,
    OTPLength,
    value,
    className,
    otpType,
    resendOTPClassName,
    disabled,
    hideErrorRow,
    validation,
    errorClassNames,
    placeholder,
    onChange,
    onResendClick,
  } = props;

  const [disableOTPLink, setDisableOTPLink] = useState(true);

  /*
    ----------------------------------------------------------------------------------------------------------------------
    Handlers: Event handler for onChange, onBlur etc
    ----------------------------------------------------------------------------------------------------------------------
  */

  const toggleOTPLink = () => {
    setDisableOTPLink(false);
  };

  const resendAndDisableLink = () => {
    onResendClick();
    setDisableOTPLink(true);
  };

  const renderButton = (buttonProps: any) => {
    return (
      <div className=" mx-auto my-0.5 pr-5">
        <div className="flex items-center">
          {/* <Text
            label="label.text.didn'tReceive.otp"
            className="text-primary-sapphireBlue-600 text-xs font-Roboto mr-2"
          /> */}
          <SimpleButton
            // disabled={buttonProps.disabled}
            onClick={buttonProps.onClick}
            label="component.button.resend.otp"
            variant={BUTTON_STYLE_TYPE.TERTIARY}
            disabled={disableOTPLink}
          />
        </div>
      </div>
    );
  };

  const renderTime = (remainingTime: any) => {
    return (
      <>
        {showTimer ? (
          <span className=" flex justify-center text-center pr-5">
            {!isTimerComplete ? (
              <>
                <span className="text-primary-timerTextColor text-xs font-Roboto">
                  {remainingTime} seconds
                </span>
              </>
            ) : (
              <Text
                className="text-primary-timerTextColor text-xs font-Roboto"
                label={"label.text.otp.expired"}
              />
            )}
          </span>
        ) : null}
      </>
    );
  };

  return (
    <div className="group grid">
      {!hideLabel && <Label label={label} labelClassNames={labelClassNames} />}
      <OTPInput
        value={value}
        onChange={onChange}
        autoFocus
        OTPLength={OTPLength}
        otpType={otpType}
        disabled={disabled}
        inputClassName={className}
        placeholder={placeholder}
      />
      {!hideErrorRow && (
        <InputError
          validation={validation}
          hideErrorRow={hideErrorRow}
          errorClassNames={errorClassNames}
        />
      )}
      {!hideResendOTP && (
        <ResendOTP
          onResendClick={resendAndDisableLink}
          className={resendOTPClassName}
          renderButton={renderButton}
          renderTime={renderTime}
          onTimerComplete={toggleOTPLink}
        />
      )}
    </div>
  );
};

export default BaseOTPBox;
